function randomNumber(min, max) {
  if (max < min) throw new Error("The max can not be less than the min: min - ".concat(min, " / max - ").concat(max));
  var minToUse = min || Number.MIN_VALUE;
  var maxToUse = max || Number.MAX_VALUE;
  var number = Math.random() * (maxToUse - minToUse) + minToUse;
  return validateNumber(number, max);
}

function validateNumber(number, max) {
  return number > max ? max : number;
}

module.exports = randomNumber;